import DigitalKeyboard from '@/components/digital-keyboard/digital-keyboard.vue';
import uniPopup from "@/components/uni-popup/uni-popup.vue"

export default {
	components:{
		DigitalKeyboard,uniPopup
	},
	data() {
		return {
			payType: 1,
			orderInfo: {},
			orderNum: null,
			amount: 0,
			hasPwd: false,
			
			//倒计时
			timer: null,
			seconds: 0, //剩余支付时间
			d: '00',
			h: '00',
			i: '00',
			s: '00',
			
			//支付密码
			code:'',
			codeLength: 0,
			lengthArr:[{},{},{},{},{},{}],
			length:6,
		};
	},
	onShow(){
		this.$request.ModelUser.getAmount().then(result => {
			if(result && result.userBalance){
				this.amount = result.userBalance;
				if(result.bankInfo){
					this.hasPwd = true;
				}
			}
		})
	},
	onLoad(options) {
		let orderNum = options.orderNum;
		this.orderNum = orderNum;
		this.$request.ModelOrder.infoOder(orderNum).then(result => {
			this.orderInfo = result || {};
			if(result && result.leftPaySeconds && result.leftPaySeconds>0){
				this.seconds = result.leftPaySeconds;
			}
		})
	},
	created: function(e) {
		this.countDown()
		this.timer = setInterval(() => {
			this.seconds--
			if (this.seconds < 0) {
				this.timeUp()
				return
			}
			this.countDown()
		}, 1000)
	},
	beforeDestroy() {
		clearInterval(this.timer)
	},
	methods: {
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirm() {
			if(this.payType == 1){ //微信支付
				this.wxPay()
			}else{
				this.zfshow();
			}
		},
		async wxPay(){
			let result = await this.$request.ModelOrder.getPayInfo(this.orderNum);
			if(result.code != 'ok'){
				this.$api.msg(result.msg);
				return;
			}
			result = result.data;
			var that = this;
			if(result.payStatus && result.payStatus == true){
				let payinfo = JSON.parse(result.data);
				uni.requestPayment({
				    provider: 'wxpay',
				    timeStamp: payinfo.timeStamp,
				    nonceStr: payinfo.nonceStr,
				    package: payinfo.package,
				    signType: 'MD5',
				    paySign: payinfo.paySign,
				    success: function (res) {
				       uni.redirectTo({
							url: '/pages/money/paySuccess?orderNum=' + that.orderNum
				       });
				    },
				    fail: function (err) {
				        that.$api.msg('您已取消支付');
				    }
				});
			}else{
				this.$api.msg('预支付失败，请稍后重试');
			}
		},
		//定时器
		timeUp() {
			clearInterval(this.timer)
		},
		countDown() {
			let seconds = this.seconds
			let [day, hour, minute, second] = [0, 0, 0, 0]
			if (seconds > 0) {
				day = Math.floor(seconds / (60 * 60 * 24))
				hour = Math.floor(seconds / (60 * 60)) - day * 24
				minute = Math.floor(seconds / 60) - day * 24 * 60 - hour * 60
				second = Math.floor(seconds) - day * 24 * 60 * 60 - hour * 60 * 60 - minute * 60
			} else {
				this.timeUp()
			}
			if (day < 10) {
				day = '0' + day
			}
			if (hour < 10) {
				hour = '0' + hour
			}
			if (minute < 10) {
				minute = '0' + minute
			}
			if (second < 10) {
				second = '0' + second
			}
			this.d = day
			this.h = hour
			this.i = minute
			this.s = second
		},
		//支付密码
		zfshow(){
			if(this.hasPwd == false){
				uni.showModal({
				    title: '请先设置支付密码',
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: `/pages/set/vfphone`
							}) 
				        }
				    }
				});
			}else{
				this.code = '';
				this.codeLength = 0;
				this.$refs.popup.open();
			}
		},
		zfclose(){
			this.$refs.popup.close();
		},
		async mmpay(){
			if(this.codeLength != this.length){
				return;
			}
			console.log(this.code); //TODO code校验
			let result = await this.$request.ModelUser.ckPwd(this.code);
			if(result == true || result == 'true'){
				result = await this.$request.ModelOrder.payBalance(this.orderNum);
				if(result.code == 'ok'){
					this.$refs.popup.close();
					uni.redirectTo({
						url: '/pages/money/paySuccess?orderNum=' + this.orderNum
					});
				}else{
				    this.$api.msg(result.msg);	
				}
			}else{
				this.code = '';
				this.codeLength = 0;
				this.$api.msg('密码不正确');
				return;
			}
		},
		contToggle(k){
			let len = this.code.length;
			if(k===''){
				this.code = this.code.substring(0,len-1);
				this.codeLength = this.code.length;
				return;
			}
			if(len >= this.length){
				return;
			}
			this.codeLength = len + 1;
			this.code += k.toString();
			if(this.length===len+1){
				return;
			}
		}
	}
}
